@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.myclass{
  overflow-x: scroll;
}
* {
margin: 0;

padding: 0%;

box-sizing: border-box; font-family: "Montserrat", sans-serif;

}

body{
    background-image: url(/covid-app/static/media/ff.e5901ff4.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    
  
}
.title{
    margin-bottom:5px;
    text-align: center;
    color: black;
    background-color: white;
    font-weight: bolder;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.container-fluid {

width: 90%; margin: auto;

}
.container{
    width:900px;
    height: 50px;
    margin:10px;
    background:rgb(0, 0, 0);
    margin-right: auto;
    margin-left: auto;
    
}

.table-responsive{

max-height: 700px;

box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;}

tr:hover {

background-color: rgb(226, 218, 218);

}

td,
th{
position: relative}
.table td,
.table th{
padding:0.75rem;
vertical-align:top;
border-top:none;
}
td:hover::after, th:hover::after {

content: ""; background-color: #f2f2f2; top: -5000px; 

position: absolute;

left: 0;

height: 5000px;

width: 100%; z-index: -1;}

.table tr td:hover:nth-child(4) { background-color: rgb(231, 83, 115); color: aliceblue; border: 0.5px solid rgb(12, 12, 12);

}

.table tr td:hover { background-color: rgb(117, 223, 122); 
color: aliceblue;
border:0.5px solid rgb(12,12,12);}
.thead-dark{font-weight:bold;
text-transform: uppercase;}


